import {_decorator, Camera, Component, EventKeyboard, input, Input} from 'cc';

const {ccclass, property} = _decorator;

@ccclass('Swipe_Camera')
export class Swipe_Camera extends Component {
  @property(Camera) camera: Camera;

  register_event() {
    input.on(Input.EventType.KEY_DOWN, this.on_keyboard_down, this);
  }

  on_keyboard_down(event: EventKeyboard) {}

  on_keyboard_up(event: EventKeyboard) {}

  on_keyboard_pressing(event: EventKeyboard) {}
}
